<template>
  <div
    :class="[
      'dropdown',
      {
        show,
      }
    ]"
  >
    <!-- v-on="inputEvents" -->
    <a
      :class="['btn', 'dropdown-toggle', buttonType]"
      href="javascript:void(0)"
      role="button"
      data-toggle="dropdown"
      aria-haspopup="true"
      :aria-expanded="show"
      @click="toggleMenu"
    >
      <!-- id="dropdownMenuLink" -->
      <i
        v-if="!noDot"
        class="m-r-10 mdi mdi-dots-horizontal"
      />
      <span v-text="title" />
    </a>
    <div
      :class="[
        'dropdown-menu',
        {
          show,
        }
      ]"
      aria-labelledby="dropdownMenuLink"
      @click.stop="1"
    >
      <template
        v-for="({ text, type, action }, index) in items"
      >
        <a
          v-if="text"
          :key="index"
          :class="`dropdown-${type || 'c'}`"
          href="javascript:void(0)"
          @click.stop="onMenu({ text, type, index, action })"
          v-text="text"
        />
        <div
          v-else
          :key="index"
          class="dropdown-divider"
        />
      </template>
    </div>
  </div>
</template>

<script>
// const inTypes = [
//     'focusin',
//     'focus',
// ]
// const outTypes = [
//     'focusout',
//     'blur',
// ]

export default {
  name: 'DropdownMenu',
  components: {

  },
  props: {
    title: {
      type: [String, Number],
      default: 'title',
    },
    items: {
      type: [Array, Object],
      default() {
        return [];
      },
    },
    type: {
      type: [String, Number],
      default: '0',
    },
    noDot: {
      default: false,
    },
  },
  data() {
    return {
      show: false,
    };
  },
  computed: {
	  buttonType() {
      const defaultType = 'btn-outline-secondary';
      return [defaultType, 'btn-danger'][this.type] ?? defaultType;
    },
  },
  created() {
  },
  methods: {
    toggleMenu() {
      this.show = !this.show;
      const { body } = document;
      if (this.show) {
        setTimeout(() => {
          body.addEventListener('click', this.toggleMenu);
        }, 0);
      } else {
        body.removeEventListener('click', this.toggleMenu);
      }
    },
    onMenu({ text, type, index, action }) {
      this.$emit('onMenu', { text, type, index, action });
      setTimeout(this.toggleMenu, 0);
    },
  },
};

</script>

