<template>
  <div class="row">
    <div class="col-12">
      <div class="card">
        <div class="card-body">
          <div class="table-responsive">
            <div
              id="zero_config_wrapper"
              class="table table-striped table-bordered dataTable no-footer"
            >
              <!-- class="dataTables_wrapper container-fluid dt-bootstrap4 no-footer" -->
              <div class="toolbar" />
              <div
                id="zero_config_info"
                class="dataTables_info"
                role="status"
                aria-live="polite"
                v-text="pageInfo"
              />
              <table
                id="zero_config"
                class="table table-striped table-bordered bg-white"
              >
                <thead>
                  <tr>
                    <th class="pb-4" style="width:10px;">
                      <label class="customcheckbox mb-3">
                        <input
                          id="mainCheckbox"
                          type="checkbox"
                          @input="$emit('check', $event.target.checked)"
                        >
                        <span class="checkmark" />
                      </label>
                    </th>
                    <slot name="head">
                      <th
                        v-for="title in titles"
                        :key="title"
                        class="table-head-left"
                        v-text="title"
                      />
                    </slot>
                  </tr>
                </thead>
                <tbody>
                  <slot />
                </tbody>
              </table>
              <div
                class="dataTables_paginate paging_simple_numbers"
              >
                <ul class="pagination">
                  <li
                    :class="['paginate_button', 'page-item', 'previous', ' ml-auto', {
                      disabled: +pagination.Page === 1,
                    }]"
                  >
                    <a
                      href="javascript:void(0)"
                      class="page-link"
                      @click="$emit('onPage', pagination.Page - 1)"
                    >上一頁</a>
                  </li>
                  <li
                    v-for="index in pagination.TotalPage"
                    :key="index"
                    :class="['paginate_button', 'page-item', {
                      active: index === pagination.Page,
                    }]"
                  >
                    <a
                      href="javascript:void(0)"
                      aria-controls="zero_config"
                      class="page-link"
                      @click="$emit('onPage', index)"
                      v-text="index"
                    />
                  </li>
                  <li
                    :class="['paginate_button', 'page-item', 'previous', ' mr-auto', {
                      disabled: +pagination.Page >= +pagination.TotalPage,
                    }]"
                  >
                    <a
                      href="javascript:void(0)"
                      class="page-link"
                      @click="$emit('onPage', pagination.Page + 1)"
                    >下一頁</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'DataTable',
  components: {
  },
  props: {
    titles: {
      type: [Array, Object],
      default() {
        return [];
      },
    },
    pagination: {
      type: [Object],
      default() {
        return {
          // page: 1,
          // totalPage: 1,
          Page: 1,
          PageLimit: 10,
          TotalNumber: 0,
          TotalPage: 1,
        };
      },
    },
  },
  data() {
    return {
    };
  },
  computed: {
    pageInfo() {
      const { Page, PageLimit, TotalNumber, TotalPage } = this.pagination;
      return `顯示第 ${(Page - 1) * PageLimit + 1} 至 ${
        Page >= TotalPage
          ? TotalNumber
          : (Page + 0) * PageLimit
      } 項結果，共 ${TotalNumber} 項`;
    },
  },
  created() {
  },
  methods: {
  },
};

</script>

<style scoped>
.table-head-left {
  text-align: left;
}
</style>
