<template>
  <button
    type="button"
    :class="[
      'btn',
      switchStyle.color,
    ]"
    style="margin-right: 3px;"
    @click="onButton()"
  >
    <i
      :class="[
        'mdi',
        switchStyle.mdi,
      ]"
      style="font-style: normal;"
    >
      {{ switchStyle.text }}
    </i>
  </button>
</template>

<script>

const styleSetting = {
  color: {
    1: 'btn-outline-danger', // 紅
    2: 'btn-outline-secondary', // 灰
    3: 'btn-outline-warning', // 黃
    4: 'btn-outline-success', // 綠
  },
  mdi: {
    1: 'mdi-plus', // 加號
    2: 'mdi-arrow-up-bold-circle', // 向上箭頭
    3: 'mdi-arrow-down-bold-circle-outline', // 向下箭頭
    4: 'mdi-delete', // 垃圾桶
    5: 'mdi-playlist-plus', // 表單
  },
  message: {
    1: '',
    2: '是否將所選項目改為「正常使用」？',
    3: '是否將所選項目改為「停止使用」？',
    4: '是否刪除所選的項目？',
  },
};

export default {
  name: 'ButtonTop',
  components: {
  },
  props: {
    forBut: {
      type: Object,
    },
    forMod: {
      type: Object,
    },
  },
  data() {
    return {
    };
  },
  computed: {
    switchStyle() {
      const stylela = {
        color: styleSetting.color[this.forBut.color],
        mdi: styleSetting.mdi[this.forBut.mdi],
        text: this.forBut.text,
      };
      console.log('debug: switchStyle -> stylela', stylela);
      return stylela || {};
    },
  },
  created() {
  },
  methods: {
    onButton() {
      const dataMod = {
        title: this.forMod.title,
        type: this.forMod.type,
        message: styleSetting.message[this.forMod.message],
        action: this.forMod.action,
      };
      this.$emit('onButton', dataMod);
    },
  },
};

</script>

